.scrollContainer {
    overflow-Y: auto;
    height: 100%;
}

.navBar {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.nav {
    height: calc(100% - 19rem);
    visibility: hidden;
    opacity: 0;
    transition: all 0s 0s;

    &.menuExpanded {
        opacity: 1;
        display: block;
        visibility: visible;
        transition: visibility 0s 0.3s;
    }

}

.bottom {
    position: absolute;
    height: auto;
    bottom: 5rem;
    width: 100%;
}